@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root {
	--red: #DB202C;
}
body {
	color: white;
	font-family: 'Helvetica Neue',Helvetica,Arial,sans-serif;
	background-color: black;
}

#root {
	position: relative;
}

.btns {
	display: flex;
	margin-top: 20px;
}
.btn {
	border-radius: 7px;
    padding: 10px 20px;
    border: none;
    font-size: 18px;
    min-width: 100px;
    display: flex;
    align-items: center;
    color: black;
    justify-content: center;
    text-decoration: none;
}
.btn:hover {
	opacity: 0.9;
}
.btn:nth-child(2) {
	margin-left: 20px;
}
.btn--play {
	background-color: white;
}
.btn--moreinfo {
	background-color: grey;
	color: white;
}
.btn__icon {
	margin-right: 7px;
}
.btn__icon--info {

}
.btn__icon--play {

}

@-webkit-keyframes loading {
	100% {
    	transform: translateX(100%);
  	}
}

@keyframes loading {
	100% {
    	transform: translateX(100%);
  	}
}
.app {}
.topbar {
	padding: 20px 40px;
    background: linear-gradient(to bottom,rgba(0,0,0,.7) 90%,rgba(0,0,0,0));
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    position: fixed;
    width: 100%;
    box-sizing: border-box;
    z-index: 1;
}
.topbar--scroll {
    background: linear-gradient(to bottom,rgba(0,0,0,1) 100%,rgba(0,0,0,0));
}
.topbar__cont {
	display: flex;
	align-items: center;
}
.topbar__item {
	margin-right: 20px;
}
.topbar__item a {
    color: white;
    text-decoration: none;
}
.topbar__item .active {
	font-weight: bold;
}
.topbar__item--logo {
	font-family: 'Bebas Neue', cursive;
	color: var(--red);
	font-size: 26px;
	margin-right: 40px;
    text-shadow: 2px 2px black;
    letter-spacing: 4px;
}
.topbar__item--logo a {
    color: var(--red);
    text-decoration: none;
}
.topbar__icon {
	-webkit-filter: invert(1);
	        filter: invert(1);
    height: 15px;
}
.topbar__acc {
	background-color: var(--red);
	width: 30px;
	height: 30px;
	position: relative;
	border-radius: 2px;
}
.topbar__face {
	background-color: white;
    content: '';
    box-shadow: 2px 0 0 0 white;
    position: absolute;
}
.topbar__face-leye {
	height: 3px;
    width: 2px;
    top: 7px;
    left: 7px;
}
.topbar__face-reye {
	height: 3px;
    width: 2px;
    top: 7px;
    right: 7px;
}
.topbar__face-mouth {
	height: 3px;
    width: 8px;
    top: 17px;
    left: 13px;
}

@media (max-width: 576px) {
    .topbar {
        padding: 20px 20px;
    }
    .topbar__item {
        margin-right: 10px;
        font-size: 12px;
    }
    .topbar__item--logo {
        margin-right: 10px;
        font-size: 26px;
    }
}
.fbp {
	position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 30px;
    background: rgba(0,0,0,0.7);
    display: none;
    z-index: 1;
}
.fbp--visible {
	display: block;
}
.fbp__close {
	position: absolute;
	top: 0;
	right: 0;
	padding: 10px;
	cursor: pointer;
}
